}
* Modal */ .ReactModalPortal > div {
  opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
  align-items: center;
  display: flex;
  justify-content: center;
  transition: opacity 0.2s ease-in-out;
  z-index: 999;
}

.modal-fondo {
  background-color: rgba(0, 0, 0, 0.3);
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
  position: fixed;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0;
}

.modal {
  background: white;
  border-radius: 5px;
  color: rgb(51, 51, 51);
  display: inline;
  max-height: 620px;
  max-width: 500px;
  outline: none;
  padding: 10px;
  font-family: "Roboto", sans-serif;
}

.react-datetime-picker input:focus {
  outline: none;
}

.react-datetime-picker__wrapper {
  border: none;
}

.fab {
  border-radius: 50%;
  position: fixed;
  font-size: 22px;
  bottom: 25px;
  font-family: "Roboto", sans-serif;
  width: 60px;
  height: 60px;
  z-index: 100;
  right: 25px;
}

.fab-danger {
  position: fixed;
  font-size: 22px;
  padding: 7px 14px;
  bottom: 25px;
  left: 25px;
  z-index: 100;
  font-family: "Roboto", sans-serif;
}

button:focus {
  outline: none;
}

button:disabled {
  cursor: not-allowed;
}

